import React from "react";
import ReactDOM from "react-dom";
import Button from "./Button";
import Card from "./Card";
import styles from "./ErrorModel.module.css";

const ErrorModel = (props) => {
  const Backdrop = (props) => {
    return (
      <div className={styles.backdrop} onClick={props.closeErrorMessage} />
    );
  };

  const Overlay = (props) => {
    return (
      <Card className={styles.modal}>
        <header className={styles.header}>
          <h2>{props.title}</h2>
        </header>
        <div className={styles.content}>
          <p>{props.message}</p>
        </div>
        <footer className={styles.actions}>
          <Button onClick={props.closeErrorMessage}>Okay</Button>
        </footer>
      </Card>
    );
  };

  return (
    <>
      {ReactDOM.createPortal(
        <Backdrop closeErrorMessage={props.closeErrorMessage} />,
        document.getElementById("backdrop-root")
      )}
      {ReactDOM.createPortal(
        <Overlay
          title={props.title}
          message={props.message}
          closeErrorMessage={props.closeErrorMessage}
        />,
        document.getElementById("overlay-root")
      )}
    </>
  );
};

export default ErrorModel;
