import React, { useRef, useState } from "react";
import Button from "../UI/Button";
import Card from "../UI/Card";
import ErrorModel from "../UI/ErrorModel";
import styles from "./AddUser.module.css";

const AddUser = (props) => {
  const enteredNameRef = useRef();
  const enteredAgeRef = useRef();
  // const [enteredUsername, setEnteredUsername] = useState("");
  // const [enteredAge, setEnteredAge] = useState("");
  const [error, setError] = useState();

  const addUserHandler = (event) => {
    event.preventDefault();
    // console.log(enteredAgeRef.current.value);
    const enteredName = enteredNameRef.current.value;
    const enteredAge = enteredAgeRef.current.value;

    if (enteredName.trim().length === 0 || enteredAge.trim().length === 0) {
      setError({
        title: "Invalid Input",
        message: "The inputs cannot be empty",
      });
      return;
    }
    if (+enteredAge < 5) {
      setError({
        title: "Invalid Age",
        message: "The age must be 6 or above",
      });
      return;
    }
    props.onAddData(enteredName, enteredAge);
    // console.log(enteredUsername, enteredAge);

    // setEnteredUsername("");
    // setEnteredAge("");
  };

  // const onUsernameChangeHandler = (event) => {
  //   setEnteredUsername(event.target.value);
  // };

  // const onAgeChangeHandler = (event) => {
  //   setEnteredAge(event.target.value);
  // };

  const closeErrorCard = () => {
    setError(null);
  };

  return (
    <>
      {error && (
        <ErrorModel
          title={error.title}
          message={error.message}
          closeErrorMessage={closeErrorCard}
        />
      )}
      <Card className={styles.input}>
        <form onSubmit={addUserHandler}>
          <label htmlFor="username">Username</label>
          <input
            id="username"
            type="text"
            // onChange={onUsernameChangeHandler}
            // value={enteredUsername}
            ref={enteredNameRef}
          />
          <label htmlFor="age">Age (Years)</label>
          <input
            id="username"
            type="number"
            // onChange={onAgeChangeHandler}
            // value={enteredAge}
            ref={enteredAgeRef}
          />
          <Button type="submit">Add User</Button>
        </form>
      </Card>
    </>
  );
};

export default AddUser;
